import React from 'react'

import Main from "../../components/InPages/Main/Main.jsx"
import Presentation from '../../components/InPages/Presentation/Presentation.jsx'
import OutrosServicos from '../../components/InPages/OutrosServicos/OutrosServicos.jsx'
import AreasAtuantes from '../../components/Aeras Atuantes/AreasAtuantes.jsx'
import NossosServicos from '../../components/NossosServicos/NossosServicos.jsx'
import PeressegurancaServices from '../../ComumVars/PeresSegurancaServices.js'

import EmpresasCard from '../../ComumVars/EmpresasCard.js'

const Seguranca_Patrimonial = () => {
  return (
    <>
      <section id="peresseguranca" className="img flex">

        <Main title="Segurança Patrimonial" slogan={""} />

        <Presentation
          title="Know-How na área"
          content={"A proteção do seu patrimônio é nossa prioridade máxima. Com anos de experiência no setor, oferecemos soluções personalizadas e tecnologias avançadas para garantir a segurança completa de sua empresa ou residência. Nossa equipe altamente treinada e comprometida está sempre pronta para responder com eficiência e rapidez a qualquer situação."}
        />

        <NossosServicos services={PeressegurancaServices}></NossosServicos>

        <AreasAtuantes />

        <OutrosServicos
          emp1={EmpresasCard[0].Genial}
          emp2={EmpresasCard[0].P3}
        />

      </section>

    </>

  )
}

export default Seguranca_Patrimonial;