import React from 'react'

import Main from "../../components/InPages/Main/Main.jsx"
import Presentation from '../../components/InPages/Presentation/Presentation.jsx'
import OutrosServicos from '../../components/InPages/OutrosServicos/OutrosServicos.jsx'
import AreasAtuantes from '../../components/Aeras Atuantes/AreasAtuantes.jsx'
import NossosServicos from '../../components/NossosServicos/NossosServicos.jsx'
import PeressegurancaServices from '../../ComumVars/PeresSegurancaServices.js'

import EmpresasCard from '../../ComumVars/EmpresasCard.js'

import "./genialpage.css"

const Genialpage = () => {
  return (
    <>
      <section id="genial" className="img">

        <Main title="Genial Serviços" slogan={"Qualidade e Confiança"} />

        <Presentation
          title="Know-How na área"
          content={"O atendimento a diversos segmentos do mercado traz a oportunidade de criar soluções de melhores práticas, envolvendo treinamento e capacitação contínua de seus profissionais, tecnologia de última geração e um time estratégico que realiza estudos completos e customizados de maior nível de segurança patrimonial e melhor custo/benefício."}
        />

        <NossosServicos services={PeressegurancaServices}></NossosServicos>

        <AreasAtuantes />

        <OutrosServicos
          emp1={EmpresasCard[0].Peres_Seguranca}
          emp2={EmpresasCard[0].P3}
        />

      </section>

    </>

  )
}

export default Genialpage;