import React, { useEffect } from 'react';
import './Apresentacao.css'
import AOS from 'aos';
import 'aos/dist/aos.css';

const Apresentacao = () => {

    useEffect(() => {
        AOS.init({
            duration: 800,
            once: true
        });
    }, []);

    return (
        <section id="apresentacao" className="img flex" >

            <div id="ap-texto" data-aos="fade-up">

                <h1>GRUPO PERES, a melhor escolha</h1>

                <p>
                    Com quase duas décadas de atuação no Ceará,
                    o Grupo Peres é a escolha ideal para a sua segurança patrimonial.
                    A nossa credibilidade e eficiência, especialmente na Peres Segurança,
                    são reconhecidas no mercado. Além disso, a confiança que os
                    consumidores depositam em nós é um testemunho da nossa
                    confiabilidade. Escolher o Grupo Peres é optar por um
                    serviço confiável, de qualidade e sólido. Estamos à sua
                    disposição para atender às suas necessidades.
                </p>

            </div>


            <div id="ap-img" className="img" data-aos="fade-up"></div>

        </section>
    )
}

export default Apresentacao